
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import Request from '../../assets/js/request.js'

export default {
components: {
XInput,
XButton,
Group,
Cell,
Flexbox,
FlexboxItem,
Divider
},
mounted() {

},
watch:{
'account_name' : function () {
let self = this;
Request.get('/api/account/pagination', {
'account_name' : self.account_name
}, function (data) {
if (data.length>0){
self.name = data[0].reference.full_name
}
})
}
},
data(){
return {
account_name : '',
current_amt : 50,
name : ''
}
},
methods:{
submitRecharge : function () {

}
}
}



