  import Request from '../../assets/js/request.js'
  export default {
    mounted() {
      console.log('Component mounted.')
    },
    data(){
      return {
        account:{},
        school:{},
      }
    },
    created: function () {
      this.getUser();
//      this.settings();
    },
    methods:{
      getUser:function(){
        let self = this;
        Request.get('/api/account/get-user', {}, function (data) {
          self.account = data;
          self.school = data.school;
        })
      }
    }


  }



