import Request from '../../assets/js/request.js'
  export default {
      mounted() {
          console.log('Component mounted.')
      },
      data(){
        return {
          record:[]
        }
      },
      created: function () {
          this.getCardRecord();
         // console.log(this.$route.params.cardId);
      },        
      methods:{
        getCardRecord:function(){
          let self = this;
          Request.get('/api/account/get-card-record', {
            'id':self.$route.query.cardId
          }, function (data) {
              self.record = data;
          })
        },
        getTypeVal:function(item)
        { 
          if (item.type == 1)
            return '充值';
          else if (item.type == 2)
            return '消费';
          else if (item.type == 3)
            return '挂失入账';
          else if (item.type == 4)
            return '挂失出账';    
          return '其他';            
        },
        getChannelVal:function(item)
        {
          if (item.channel == 1)
            return '现金';
          else if (item.channel == 2)
            return '刷卡';
          else if (item.channel == 3)
            return '微信';
          else if (item.channel == 4)
            return '支付宝';    
          return '其他'; 
        },
        checkAmtType:function(item)
        {
          if (item.type == 1 || item.type == 4)
            return true;
          else if (item.type == 2 || item.type == 3)
            return false;
        }
         
  


      }

  }



